import style from './order-details.module.css';

import iconDone from '../../images/done.jpg'
import { useSelector } from "react-redux";

const OrderDetails = () => {

    const { orderID, feedRequest, feedFailed } = useSelector(state => state.order)

    if (feedRequest && !orderID) {
        return  <h4> </h4>
    } else if (feedFailed) {
        return <h5> </h5>
    }

    return (
        <div className={style.wrapper}>
            <p className='text text_type_digits-large'>{orderID}</p>
            <p className="text text_type_main-medium mt-8"> </p>
            <div className='mt-15'>
                <img src={iconDone} alt=" "/>
            </div>
            <p className="text text_type_main-default mt-15">   </p>
            <p className={`${style.paragraph} text text_type_main-default mt-2`}>    </p>
        </div>
    )
}

export default OrderDetails;